"use client";

import { useState } from "react";
import { Send } from "lucide-react";

import SectionHeading from "@/src/components/layouts/SectionHeading";
import Button from "@/src/components/base/Button";
import Input from "@/src/components/base/Input";
import Select from "@/src/components/base/Select";
import Textarea from "@/src/components/base/TextArea";

import { COURSE_OPTIONS } from "../_utils/components/constants/courseOptions";

export default function ContactForm() {
  const [loading, setLoading] = useState(false);

  function handleSubmit(e: React.FormEvent<HTMLFormElement>) {
    e.preventDefault();
    setLoading(true);

    setTimeout(() => {
      setLoading(false);
      alert("Message sent successfully!");
    }, 1500);
  }

  return (
    <section className="py-8">
      <SectionHeading
        title="Send a message"
        subtitle="Fill out the form below and our team will get back to you shortly. Weâ€™re happy to guide you in choosing the right course."
        align="center"
      />

      <div className="bg-gray-50 border border-gray-200 rounded-3xl p-8 md:p-10 shadow-sm max-w-3xl mx-auto">
        <form onSubmit={handleSubmit} className="grid grid-cols-1 gap-6">
           <Input label="Full Name" 
           required 
           placeholder="Your name" 
           id={""} 
           type={"text"}
            value={""} 
            onChange={function (_value: string | number): void {
              throw new Error("Function not implemented.");
            }}
             />

  <Input type="email" label="Email Address" required placeholder="you@example.com" id={""} value={""} onChange={function (_value: string | number): void {
              throw new Error("Function not implemented.");
            }} />


 <Input type="text" label="Phone / WhatsApp" placeholder="+94 7X XXX XXXX" id={""} value={""} onChange={function (_value: string | number): void {
              throw new Error("Function not implemented.");
            }} />


          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">
             
            </label>
         
          </div>

          <div>
     
            <Textarea
            label="Message"
              rows={5}
              placeholder="Tell us about your goals or questions..."
            />
          </div>

          <div className="text-center">
            <Button
              type="submit"
              variant="rounded"
              size="lg"
              disabled={loading}
            >
              {loading ? "Sending..." : "Send Message"}
              <Send className="w-5 h-5" />
            </Button>
          </div>
        </form>
      </div>
    </section>
  );
}
